@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap");

.skills-page_container {
  position: relative;
  width: 100%;
  min-height: calc(100vh + 5px);

  padding-top: 140px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Montserrat", sans-serif;

  background-image: linear-gradient(
      to bottom,
      rgba(20, 38, 65, 1),
      rgba(33, 66, 116, 0.5)
    ),
    url("images/background-skills.png");

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  z-index: 5;
}

.skills-page_inside_container {
  position: relative;
  width: 90%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.skills-window_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 24%;
  background: #051937;
  padding: 0.9vw;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  opacity: 0.5;
  transition: 1s;
}

.skills-window_container:hover {
  opacity: 1;
}

.skills-window_container h1 {
  font-size: 1.5vw;
  text-align: center;
  color: #fff;
  margin: 0;
  padding: 0;
}

.skills-window_container p {
  font-size: 0.7vw;
  color: #fff;
}

.skill_container {
  position: relative;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.1);
  margin: 0.1vw 0;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.5);
}

.skill-label_container h3 {
  font-size: 0.8vw;
  font-weight: 700;
  margin: 0;
  padding: 0;
  color: #fff;
  float: left;
}

.skill-percentage {
  font-size: 0.8vw;
  color: #fff;
  float: right;
  opacity: 0;
  transition: 1s;
}

.skills-window_container:hover .skill-percentage {
  opacity: 1;
}

.skill-bar_container {
  position: relative;
  width: 100%;
  height: 2px;
  background: #051937;
  margin: 0.5vw 0;
}

.skill-progress_container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: linear-gradient(to right, #0b2752, #2b80ff);
  transition: 1s;
}

.front-end-skills-window_container:hover .skill-js-progress_container {
  width: 90%;
}

.front-end-skills-window_container:hover .skill-html-css-progress_container {
  width: 80%;
}

.front-end-skills-window_container:hover .skill-react-progress_container {
  width: 75%;
}

.front-end-skills-window_container:hover .skill-redux-progress_container {
  width: 60%;
}

.front-end-skills-window_container:hover .skill-lit-wc-progress_container {
  width: 60%;
}

.front-end-skills-window_container:hover .skill-sapui5-progress_container {
  width: 40%;
}

.back-end-skills-window_container:hover .skill-node-progress_container {
  width: 55%;
}

.back-end-skills-window_container:hover .skill-cap-progress_container {
  width: 50%;
}

.back-end-skills-window_container:hover .skill-api-progress_container {
  width: 50%;
}

.back-end-skills-window_container:hover .skill-server-progress_container {
  width: 40%;
}

.back-end-skills-window_container:hover .skill-python-progress_container {
  width: 35%;
}

.back-end-skills-window_container:hover .skill-databases-progress_container {
  width: 30%;
}

.other-skills-window_container:hover .skill-git-progress_container {
  width: 80%;
}

.other-skills-window_container:hover .skill-agile-progress_container {
  width: 65%;
}

.other-skills-window_container:hover .skill-wordpress-progress_container {
  width: 50%;
}

.other-skills-window_container:hover .skill-ms-progress_container {
  width: 50%;
}

.other-skills-window_container:hover .skill-deployment-progress_container {
  width: 40%;
}

.language-skills-window_container:hover .skill-ua-progress_container {
  width: 100%;
}

.language-skills-window_container:hover .skill-rus-progress_container {
  width: 100%;
}

.language-skills-window_container:hover .skill-pl-progress_container {
  width: 90%;
}

.language-skills-window_container:hover .skill-en-progress_container {
  width: 75%;
}

.language-skills-window_container:hover .skill-de-progress_container {
  width: 10%;
}

.skill-progress_container:before {
  content: "";
  position: absolute;
  top: -5px;
  right: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.skills-window_container:hover .skill-progress_container:before {
  background: #fff;
  box-shadow: 0 0 25px rgba(41, 123, 247, 1);
}

@media only screen and (max-width: 1200px) {
  .skills-page_container {
    padding: 40px 0;
  }

  .skills-page_inside_container {
    min-height: 100%;
    flex-direction: column;
    align-items: center;
  }

  .skills-window_container {
    width: 100%;
    height: 45%;
    padding: 20px;
    margin-bottom: 40px;
  }

  .skills-window_container:last-child {
    margin-bottom: 0;
  }

  .skills-window_container h1 {
    font-size: 5vw;
  }

  .skills-window_container p {
    font-size: 3vw;
  }

  .skill_container {
    padding: 10px 20px;
    margin: 1vw 0;
  }

  .skill-label_container h3 {
    font-size: 3vw;
  }

  .skill-percentage {
    font-size: 3vw;
  }

  .skill-bar_container {
    margin: 3vw 0;
  }
}
