.nav_main {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
  z-index: 10;
  transition: 0.6s;
}

.nav_main.sticky {
  padding: 0px 100px;
  background: #fff;
  box-shadow: 1px 1px 50px #000;
}

.logo_main_container {
  margin: 20px 0;
  background: linear-gradient(89deg, #cfd6e6 0%, #e7eff9 100%);
}

.nav_main.sticky .logo_main_container {
  margin: 10px 0;
}

.logo_main {
  position: relative;
  padding: 0;
  margin: 0;
  height: 100px;
}

.ul_menu_main {
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
}

.li_menu_main_item {
  position: relative;
  list-style-type: none;
  transition: 0.5s;
  padding: 40px 0;
}

